:root {
    --main-color: #e67e22;
    --black-color: #0e0e0e;
    --secondary-color: #343232;
    --secondary-text-color: #9b9a9a;
    --white-color: #f5f6fa;

    --hover-color: ;

    --bg-color: ;
    --card-bg: ;

}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    border: 0;
    outline: 0;
    text-transform: capitalize;
}


html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Tilt Neon';
}


section {
    min-height: 100vh;
}

.btn-main {
    background-color: var(--main-color);
    font-size: 1.3rem;
    border: .3rem solid var(--main-color);
    padding: .5rem 1rem;
    font-weight: 700;
    color: white;
    transition: .5s ease;
}

.btn-main:hover {
    color: var(--main-color);
    border-color: var(--main-color);
}

.content-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-container {
    gap: 2rem;
    display: grid;
    justify-content: center;
    grid-template-columns: auto auto auto;
    align-items: center;
}

.page-heading {
    padding: 2rem;
    text-align: center;
}

.page-heading h3 {
    font-weight: 700;
    font-size: 2rem;
    /* text-shadow: 0 0 .5rem var(--main-color); */
    text-decoration: underline;
    text-decoration-style: double;
    text-decoration-color: var(--main-color);
}

.page-heading p {
    font-size: 1rem;
}

.page-divider {
    height: 65vh;
    background-image: url("https://c1.wallpaperflare.com/preview/630/189/101/kutch-festival-rann-of-kutch-festival-kutch-tourism.jpg");
    background-position: center;
    /* background-attachment: fixed; */
    background-size: cover;
    color: white;
}

.page-divider .inner {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: auto auto;
    gap: 2rem;
    padding: 1rem;
}

.page-divider .inner .card {
    border-radius: 0;
}

.page-divider .inner .card .card-img-overlay {
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
}

.page-divider .inner .card .card-img-overlay:hover {
    background-color: rgba(0, 0, 0, 0.597);
}





/* Header styling  */
header.navbar {
    z-index: 999;
    position: fixed;
    width: 100%;
    top: 0;
    padding-top: 1rem;
    left: 0;

    background: white;

}

header .navbar-brand {
    font-family: Bagel Fat One;
}

header .navbar-brand span {
    color: var(--main-color);

}

/* header.navbar.sticky {
    background-color: white;
} */



/* Home styling */
#home {
    background-image: url('https://img3.wallspic.com/previews/0/0/0/3/7/173000/173000-norway_nature-fjord-lofoten-nature-travel-x750.jpg');

    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    color: white;
}

#home .home-content h3.main {
    font-size: 5rem;
    font-weight: 900;
    font-family: 'Bagel Fat One';
}

#home .home-content h3.main span {
    color: var(--main-color);
    text-decoration: underline;
    text-decoration-color: white;
}

#home .home-content h4.basic {
    font-size: 2rem;
    font-family: 'sail';
}

.travel-form {
    padding: 2rem;
    background-color: var(--black-color);
}

.travel-form form {
    padding: 0 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.travel-form form div {}

/* Treding styling */
#trending {}

#trending .card {
    border: 0;
    border-radius: 0;
    background: transparent;
    font-size: .9rem;
}

#trending .card .card-body {
    padding: .5rem;
}

#trending .card .card-header {
    background: transparent;
    border: 0;
    padding: .7rem;
    padding-bottom: 0;
    font-size: 1.2rem;
    font-weight: 700;
}

#trending .card img.card-img-top {
    /*  */
}















/* Destination Styling */

#destination .box {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

#destination .box img {
    width: 5rem;
}

#destination .img-container {
    height: 65vh;
    overflow: hidden;
    display: grid;
    gap: 2rem;
    grid-template-columns: auto auto auto;
}

#destination .row .col-md {
    padding: 0 !important;
}

#destination .row .col-md img {
    width: 100%;
    height: 100%;
}

footer {
    text-align: center;
    color: var(--white-color);
    background-color: var(--black-color);
    /* padding: 2rem; */
}

footer .container {
    padding: 4rem 2rem;
}

footer h4 {
    font-size: 1.3rem;
    font-weight: 600;
    padding-bottom: 1rem;
}

footer p {
    padding-top: 0;
    padding: 1rem 0;
    color: var(--secondary-text-color);
    font-size: 1rem;
}

footer .card {
    margin-bottom: 1rem;
    text-align: left;
    background: transparent;
    font-size: .6rem;
}

footer .card img {
    height: 80%;
}

footer .card .card-body {
    padding-left: .5rem !important;
    padding: 0;
}

footer .card .card-title {
    font-size: 1rem;
    padding: 0;
    color: var(--white-color);
}

footer .card .card-text {
    margin: 0;
    padding: 0;
    font-size: .8rem;
}

footer .destination {
    text-align: left;
}

footer .destination a {
    background: transparent;
    color: var(--secondary-text-color);
    border: none;
}

footer .destination a i {
    font-size: 1.3rem;
    margin-right: 1rem;
    color: var(--main-color);
}

footer .social-icon {
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

footer .social-icon a i {
    color: var(--main-color);
    padding: .3rem;
    border-radius: 50%;
    font-size: 1.5rem;
    background-color: var(--white-color);
}

footer .bottom-footer {
    text-align: center;
    padding: 1rem;
    color: var(--secondary-text-color);
    background-color: var(--secondary-color);
}